<template>
  <div id="tablet">
    <div class="queryBox">
      <QueryBox :formArr="queryFrom" @query="getQueryData" style="width: calc(100% - 75px)"></QueryBox>
      <div class="queryBtns">
        <Button type="primary" size="small" icon="md-add" class="btn-add" @click.stop="openAdd('add', '')">新增</Button>
      </div>
    </div>
    <div class="content_box">
      <div>
        <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row :max-height="tableHeight"></Table>
        <div class="page_box">
          <Page :total="total" ref="pages" :page-size="pageSize" :current.sync="pageNum" @on-change="pageChange" show-total/>
        </div>
      </div>
    </div>
    <!-- 查看-->
    <div class="extract_box" :class="{addActive:addActive}">
      <activitiesInspectTableMap ref="achievementTableMap" @signOut="signOut"></activitiesInspectTableMap>
    </div>
    <!-- 新建-->
    <Modal
      :title="modalStatus + '任务成果审核'"
      v-model="modal">
      <Form ref="addForm" :model="AddData" :rules="ruleValidate" :label-width="90" style="padding:0 20%;">
        <FormItem prop="name" label="任务名称：">
          <Input v-model="AddData.name" placeholder="请输入任务名称" size="default"/>
        </FormItem>
        <FormItem prop="year" label="任务年份：">
          <DatePicker size="default" type="date" v-model="AddData.year" placement="bottom-end" placeholder="请选择任务年份" style="width: 100%"></DatePicker>
        </FormItem>
        <FormItem prop="type" label="任务类型：">
          <Select v-model="AddData.type" size="default" placeholder="请选择任务类型">
            <Option v-for="item in addTypeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
          </Select>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="addSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import QueryBox from '@/components/publicQuery'
import activitiesInspectTableMap from './achievementTable_map'
import {
  // 表格列表
  getSurveyTableList,
  // 删除单条记录---暂时
  infoDel,
  // 新建活动核查---暂时
  saveFiled
} from '@/api/dataCollection'
export default {
  name: 'achievementTable',
  data () {
    return {
      /* 查询框查询条件 */
      queryData: {},
      typeList: [
        {
          value: '',
          label: '请选择'
        },
        {
          value: '1',
          label: 'type1'
        },
        {
          value: '2',
          label: 'type2'
        }
      ],
      /* 表格高度 */
      tableHeight: Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40,
      /* 表格数据 */
      columns1: [
        {
          title: '序号',
          fixed: 'left',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '任务名称',
          fixed: 'left',
          align: 'center',
          minWidth: 200,
          key: 'srld_code'
        },
        {
          title: '任务年份',
          align: 'center',
          minWidth: 100,
          key: 'srld_code'
        },
        {
          title: '任务类型',
          align: 'center',
          minWidth: 120,
          key: 'srld_code'
        },
        {
          title: '任务坐标',
          align: 'center',
          minWidth: 200,
          key: 'srld_code'
        },
        {
          title: '点位类型',
          align: 'center',
          minWidth: 200,
          key: 'srld_code'
        },
        {
          title: '任务描述',
          align: 'center',
          minWidth: 200,
          key: 'srld_code'
        },
        {
          title: '操作',
          key: 'address',
          width: 280,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small',
                  icon: 'md-eye'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.addActive = true
                    this.$refs.achievementTableMap.initCom()
                  }
                }
              }, '查看问题点'),
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small',
                  icon: 'md-create'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.openAdd('modify', params.row)
                  }
                }
              }, '修改'),
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small',
                  icon: 'md-trash'
                },
                on: {
                  click: () => {
                    this.$Modal.confirm({
                      title: '确认',
                      content: '请问您确定要删除该条数据吗？',
                      onOk: () => {
                        this.infoDel(params.row.id)
                      },
                      onCancel: () => {
                      }
                    })
                  }
                }
              }, '删除')
            ])
          }
        }
      ],
      data1: [],
      /* 总条数 */
      total: 0,
      pageNum: 1,
      pageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      /* 加载 */
      loading: false,
      /* 提取信息框状态 */
      addActive: false,
      // 添加
      modal: false, // 添加的弹框
      modalStatus: '',
      /* 添加的弹框-表单参数 */
      AddData: {
        name: '',
        year: '',
        type: ''
      },
      /* 添加的弹框-表单校验 */
      ruleValidate: {
        name: [
          { required: true, message: '任务名称必须填写', trigger: 'blur' }
        ],
        year: [
          { required: true, type: 'date', message: '任务年份必须选择', trigger: 'change' }
        ],
        type: [
          { required: true, message: '任务类型必须选择', trigger: 'change' }
        ]
      },
      /* 添加的弹框-类型参数 */
      addTypeList: [
        {
          value: '1',
          label: 'type1'
        },
        {
          value: '2',
          label: 'type2'
        }
      ]
    }
  },
  components: {
    QueryBox,
    // 人类活动核查信息地图
    activitiesInspectTableMap
  },
  computed: {
    // 查询表单
    queryFrom: function () {
      return [
        {
          type: 1,
          field: 'name',
          title: '任务名称'
        },
        {
          type: 3,
          field: 'year',
          title: '任务年份'
        },
        {
          type: 2,
          field: 'type',
          title: '任务类型',
          data: this.typeList
        }
      ]
    }
  },
  created () {
    this.getTableList()
  },
  watch: {
    queryData () {
      this.getTableList()
    }
  },
  mounted () {
    // 窗口改变时table自适应
    window.addEventListener('resize', () => {
      this.tableHeight = Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40
      this.pageSize = Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48)
      this.getTableList()
    }, false)
  },
  methods: {
    /* 获取查询数据 */
    getQueryData (data) {
      this.queryData = data
      this.pageNum = 1
      this.getTableList()
    },
    /* 获取字段列表 */
    getTableList () {
      this.loading = true
      getSurveyTableList({
        'data': {
          pageSize: this.pageSize,
          pageNum: this.pageNum,
          ...this.queryData
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.rows
          this.total = res.data.data.total
          this.loading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getTableList()
    },
    /* axios: 删除数据 */
    infoDel (e) {
      infoDel({
        data: {
          id: e
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.getTableList()
          this.$Notice.success({ title: '删除成功' })
        } else {
          this.$Notice.error({ title: '删除失败' })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 信息提取地图页面退出 */
    signOut (addActive) {
      this.addActive = addActive
    },
    /* 添加核查任务 */
    addSubmit () {
      this.$refs.addForm.validate(valid => {
        if (valid) {
          saveFiled({
            data: {
              name: this.AddData.name,
              year: this.AddData.year,
              type: this.AddData.type
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: this.modalStatus + '任务成果成功' })
              this.modal = false
              this.getTableList()
            } else {
              this.$Notice.error({ title: this.modalStatus + '任务成果失败' })
            }
          })
        }
      })
    },
    /* 打开弹框 */
    openAdd (status, row) {
      this.modal = true
      // 清空表单校验
      this.$refs.addForm.resetFields()
      if (status === 'add') {
        this.$nextTick(() => {
          this.modalStatus = '新增'
        })
      } else if (status === 'modify') {
        // 把列表中单条数据赋给修改模态框
        this.AddData = row
        this.$nextTick(() => {
          this.modalStatus = '修改'
        })
      }
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .iconfont {
      margin-right: 4px;
      &.icon-xuanzewenjian {
        font-size: 12px;
      }
      &.icon-xiazai {
        font-size: 13px;
      }
      &.icon-upload {
        font-size: 12px;
      }
    }
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .content_box {
      margin: 0 15px 0;
      .page_box{
        width: 100%;
        padding: 15px;
        text-align: right
      }
    }
    .extract_box{
      height: 100%;
      width: 100%;
      position: absolute;
      right: -120%;
      top:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .addActive{
      right:0;
    }
    .btn-add {
      background-color: #01c296;
      border-color: #01c296;
      &:hover, &:focus {
        background-color: #05ab85;
        border-color: #05ab85;
      }
    }
  }
</style>
